import { List, ListItem } from '@mui/material';
import PropTypes from 'prop-types';

export const Info = ({ info }) => {
  return (
    <List>
      {!info.status ? (
        <ListItem>:  </ListItem>
      ) : (
        <ListItem>: {info.status}</ListItem>
      )}
      {!info.sender ? (
        <ListItem>:  </ListItem>
      ) : (
        <ListItem>: {info.sender}</ListItem>
      )}
      {!info.recipient ? (
        <ListItem>:  </ListItem>
      ) : (
        <ListItem>: {info.recipient}</ListItem>
      )}
    </List>
  );
};

Info.propTypes = {
  info: PropTypes.objectOf(PropTypes.string).isRequired,
};
